.App {
  text-align: center;
  padding: 30px;
}

table {

  margin: 0 auto;
  border-collapse: collapse;
  width: 100%;
}
table td {
  width: 5%;
  border: 1px solid rgb(216, 207, 207);
  text-align: center;
  font-size: 18px;
}

table td:nth-child(6){
  width: 20%
}

table td:nth-child(1){
  width:8%
}

table td:nth-child(2){
  width:10%
}

table td:nth-child(7){
  width:8%
}

table tr td {
  font-size: 16px;
  height: 45px;
}

.logo{
  margin-top: 60px;
  width: 20%;
}

h3 {
  text-align: center;
  flex: 1 1 30%;
}
h3:nth-child(1){
  text-align: left;
}

h3:nth-child(3){
  text-align: right;
}

table tr td button{
  font-weight: bold;
  background: none;
  border: none;
  /* border-bottom: 1px solid; */
  text-decoration: underline;
  cursor: pointer;
}

.appbar{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.appbar div{
  flex: 1 1 30%;
}

.appbar div:nth-child(1){
  text-align: left;
}

.appbar div:nth-child(3){
  text-align: right;
}




.appbar img{
  width: 48px;
}

.state-wrap{
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}

.state-wrap div{
  display: flex;
  align-items: center;
}

.orderinfo-table{
  width: 100%;
}

.orderinfo-table td{
  width: 10%;
}

.orderinfo-table td:nth-child(3){
  width: 25%;
}

.orderinfo-table td:nth-child(4){
  width: 20%;
}

.orderinfo-table td:nth-child(5){
  width: 5%;
}
.orderinfo-table td:nth-child(6){
  width: 8%;
}

.info-acc{
  width: 100%;
  height: 40px;
  border: 1.5px solid #000000;
  border-radius: 10px;
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.info-acc img{
  width: 38px;
}

.info-acc-open{
  width: 100%;
  height: 300px;
  border: 1.5px solid #000000;
  border-radius: 10px;
  margin-bottom: 20px;
}

.info-acc-open-top{
  height: 40px;
  display: flex;
  justify-content: space-between;
  cursor: pointer;
  align-items: center;
  border: none;
}

.info-acc-open-top img{
  width: 38px;
  transform: rotate(180deg);
}

.info-acc-table{
  display: flex;
  flex-wrap: wrap;
  font-size: 16px;
  align-items: center;
}

.info-acc-table table{
  margin-top: 20px;
  flex: 1 1 40%;
  border: none;
}

.info-acc-table table td{
  border: none;
  font-size: 16px;
  height: 40px;
  display: table-cell;
  vertical-align: middle;
}

.info-acc-table table tr{
  border: none;
}

.info-acc-table table tr td{
  border: none;
}

.fourth-td{
  height: 60px;
} 

.save-btn{
  width: 50%;
  height: 50px;
  font-size: 18px;
}

.btn-wrap button{
  margin-left: 20px;
  height: 40px;
  width: 100px;
}